a {
  text-decoration: none;
}

.blind {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  overflow: hidden;
}

.pc_only {
  display: block;
}
@media (max-width: 767px) {
  .pc_only {
    display: none;
  }
}

.mobile_only {
  display: none;
}
@media (max-width: 767px) {
  .mobile_only {
    display: block;
  }
}

img {
  max-width: 100%;
}

b {
  font-weight: 700;
}

/* Components */
.tags-list {
  max-width: 630px;
  margin: 110px auto 0;
}
.tags-list .tags-list {
  text-align: center;
  font-size: 0;
}
.tags-list .tag {
  display: inline-block;
  overflow: hidden;
  max-width: 100%;
  height: 34px;
  padding: 0 15px;
  border-radius: 34px;
  font-size: 13px;
  line-height: 34px;
  color: #333333;
  background-color: #cccccc;
  vertical-align: top;
  letter-spacing: -0.5px;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.tags-list .tag ~ .tag {
  margin-left: 7px;
}

.btn {
  display: block;
  width: 180px;
  padding: 15px 24px;
  background: #ffffff;
  border: 1px solid #333333;
  font-size: 14px;
  font-weight: 700;
  letter-spacing: -0.5px;
  color: #333333;
  text-align: center;
  box-sizing: border-box;
}
.btn.btn--radius {
  border-radius: 30px;
}
.btn.btn-color--red {
  border-color: #E60012;
  background-color: #E60012;
  color: #ffffff;
}
.btn.btn-color--orange {
  border-color: #ffa051;
  background-color: #ffa051;
  color: #ffffff;
}
.btn.btn-color--blue {
  border-color: #008fd4;
  background-color: #008fd4;
  color: #ffffff;
}
.btn.btn-color--darkBlue {
  border-color: #0062a2;
  background-color: #0062a2;
  color: #ffffff;
}
.btn.btn-color--gray80 {
  border-color: #7D7D7D;
  background-color: #7D7D7D;
  color: #ffffff;
}
.btn.btn-color--gray50 {
  border-color: #999999;
  background-color: #999999;
}
.btn.btn-color--gray30 {
  border-color: #C4C4C4;
  background-color: #C4C4C4;
}
.btn.btn-color--gray20 {
  border-color: #E3E3E3;
  background-color: #E3E3E3;
}
.btn.btn-color--gray10 {
  border-color: #F5F5F5;
  background-color: #F5F5F5;
}

.popup-open {
  overflow: hidden;
}

.popup-backdrop {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1040;
  background-color: #000;
  opacity: 0.5;
  z-index: 1000;
}

.layer {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1050;
  display: none;
  overflow: hidden;
  -webkit-overflow-scrolling: touch;
  outline: 0;
  text-align: center;
  overflow-x: hidden;
  overflow-y: auto;
  z-index: 1050;
}
@media (max-width: 767px) {
  .layer {
    text-align: initial;
  }
}
.layer:before {
  content: "";
  display: inline-block;
  height: 100%;
  vertical-align: middle;
}
@media (max-width: 767px) {
  .layer:before {
    display: none;
  }
}
.layer-popup {
  display: inline-block;
  position: relative;
  max-width: 360px;
  width: 100%;
  margin: 0 auto;
  border-radius: 20px 20px 0px 0px;
  text-align: left;
  vertical-align: middle;
  opacity: 0;
}
@media (max-width: 767px) {
  .layer-popup {
    position: fixed;
    bottom: 0;
    max-width: 100%;
  }
}
.layer .box .popup-body {
  padding: 24px;
  background-color: #ffffff;
  border-radius: 20px 20px 0px 0px;
  color: #333333;
}
.layer .box .popup-body h1 {
  margin: 0;
  font-size: 30px;
  color: #333333;
  text-align: left;
}
.layer .box .popup-body h2 {
  padding-top: 6px;
  font-size: 20px;
}
.layer .box .popup-body h3 {
  padding-top: 11px;
  font-size: 15px;
}
.layer .box .popup-body a {
  display: block;
  max-width: 192px;
  margin-top: 35px;
  padding: 15px 0;
  background-color: #ce4647;
  border-radius: 40px;
  font-size: 16px;
  color: #ffffff;
  text-align: center;
}
.layer .box .popup-footer {
  padding: 23px 16px;
  background-color: #ffffff;
  border-top: 1px solid #cccccc;
  font-size: 14px;
  font-weight: 400;
}
.layer .box .popup-footer button {
  background-color: transparent;
  border: 0 none;
  color: rgba(0, 0, 0, 0.5);
  cursor: pointer;
}
.layer .box .popup-footer button.close-popup {
  float: right;
}
.layer.in .layer-popup {
  opacity: 1;
}

.tab-container .tab-menu {
  background-color: #ffffff;
  border: 1px solid #ccc;
}
.tab-container .tab-menu ul {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.tab-container .tab-menu ul li {
  flex: 1;
  text-align: center;
}